var that;
export default {
	data() {
		return {
			TabCur: 0,
			tx: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg',
			lsit: [],
			iheight: '',
			page: 1,
			page1: 1,
			alllist: [],
			zlist: [],
			allnomore: false,
			noall: false,
			znomore: false,
			nozlist: false,
			first: 0,
			second: 0
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		},
		getselect(e) {
			this.TabCur = e.detail.current;
		},
		// 获取全部团队列表
		getalllist() {
			if (that.allnomore) {
				return;
			}
			that.noall = false;
			this.$request
				.get(
					{
						url: 'api/share/get-team',
						data: {
							page: that.page,
							status: 0
						}
					},
					'正在加载'
				)
				.then(res => {
					if (res.code == 1) {
						this.$alert(res.msg);
					} else {
						console.log(res);
						that.first = res.data.first;
						that.second = res.data.second;
						if (res.data.list.length > 0) {
							that.alllist = that.alllist.concat(res.data.list);
							that.page = that.page + 1;
							console.log(that.alllist);
						} else {
							if (that.page == 1) {
								that.noall = true;
							}
							that.allnomore = true;
						}
					}
				});
		},
		getzlist() {
			if (that.znomore) {
				return;
			}
			that.nozlist = false;
			this.$request
				.get(
					{
						url: 'api/share/get-team',
						data: {
							page: that.page1,
							status: 1
						}
					},
					'正在加载'
				)
				.then(res => {
					if (res.code == 1) {
						this.$alert(res.msg);
					} else {
						console.log(res);
						if (res.data.list.length > 0) {
							that.zlist = that.zlist.concat(res.data.list);
							that.page1 = that.page1 + 1;
						} else {
							if (that.page1 == 1) {
								that.nozlist = true;
							}
							that.znomore = true;
						}
					}
				});
		}
	},
	onLoad() {
		that = this;
		that.getalllist();
		that.getzlist();
		uni.getSystemInfo({
			success(res) {
				console.log('asjd:', res);
				that.iheight = res.windowHeight;
			}
		});
	},
	onPullDownRefresh() {
		that.alllist = [];
		that.page = 1;
		that.zlist = [];
		that.page1 = 1;
		that.getalllist();
		that.getzlist();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	}
};
